:root {
  --bg-color: #1f242d;
  --second-bg-color: #323946;
  --text-color: #fff;
  --main-color: rgb(0, 255, 115);
}

html {
  font-size: 62.5%;
  /* overflow-x: hidden; */
}

body {
  background-color: var(--bg-color);

}

section {
  min-height: auto;
  padding: 0px;              /*to remove extra space*/ 
  color: var(--text-color);
}
.navbar{
  margin: 0 5px;
}
.navbar a {
  text-decoration: none;
  padding: 1rem;
  font-size: 1.5rem;
  margin: 0 40 0 0;
  color: var(--text-color);
}

.navbar a:hover,
.navbar a.active {
  color: var(--main-color);
}

.container {
  padding: 0;
  /* position: fixed; */
  top: 0;
  left: 0;
  width: 100vw;
}

.header {
  /* padding: 5px; */            
  color: black;
  background-color: var(--bg-color);
  display: flex;
  justify-content:space-evenly;
  align-items: center;
  /* z-index: 3; */
}

.header.sticky {
  border-bottom: .1rem solid rgba(0, 0, 0, .2);
}

.logo {
  text-decoration: none;
  font-size: 5.5rem;
  color: var(--main-color);
  font-weight: 600;
  cursor: default;
  padding-left: 5rem;
}

.home-img img {
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
   height: 100%;       /*for responsive img   */
  width: 100%;       /*for responsive img   */
}


.home-content h3 {
  font-size: 3.2rem;
  font-weight: 700;
}

span {
  color: var(--main-color);
}

.heading {
  font-size: 4rem;
  text-align: center;
}

.home-content h1 {
  font-size: 5.6rem;
  font-weight: 700;
  line-height: 1.3rem;
}

.home-content p {
  font-size: 1.8rem;
  font-weight: 200;
}

.social-media a {
  display: inline-flex;
  font-size: 2rem;
  width: 2rem;
  height: 2rem;
  text-decoration: none;
  color: var(--main-color);
  border: .2rem solid var(--text-color);
  border-radius: 50%;
  padding: 0.5rem;
  background: transparent;
  margin: 1rem 1rem 2rem 0;
}

.social-media a:hover {
  color: var(--second-bg-color);
  background-color: var(--main-color);
  box-shadow: 0 0 var(--main-color);
  transition: .7s ease;
  scale: 1.5;
}

.btn {
  display: inline-block;
  text-decoration: none;
  font-size: 2rem;
  padding: 1.2rem;
  background-color: var(--main-color);
  color: var(--second-bg-color);
  font-weight: 700;
  border-radius: 4rem;
  margin: 1.5rem;
  letter-spacing: .1rem;
  box-shadow: 0 0 .7rem var(--main-color);
}

.btn:hover {
  box-shadow: none;
}

/* about*/
.about{
  background-color: var(--second-bg-color);
  display: flex;
  flex-direction: column;
  /* gap: 10%; */
  align-items: center;
  

}
.about-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4rem;
  
}

.about-content {
  margin-left: 5rem;
  /* margin: 1rem 1rem; */
}

.about h2 {
  text-align: left;
  margin-bottom: .8rem;
}

.about h3 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.about p {
 text-align: center;
  font-size: 1.8rem;
}

/* .services{
  color: var(--text-color);
} */
/* .services h2{
  margin-bottom: 5rem;
} */
/* .services h2:first-child{
  font-size: 4rem;
  text-align: center;
  color: var(--text-color);
} */
.services h2 {
  margin-top: 0%;
  /* max-width: 100%; */
}

.service-content {
  /* color: var(--text-color); */
  font-size: 1.5rem;
}

.service-content i {
  font-size: 5rem;
  color: var(--main-color);
}

.service-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 2rem;
}

.service-container .service-content {
  flex: 1 1 30rem;
  background-color: var(--second-bg-color);
  border: .1rem solid var(--second-bg-color);
  padding: 3rem 2rem 4rem;
  border-radius: 2rem;
  text-align: center;
  transition: .5s ease;

}

.service-content h3 {
  font-size: 3rem;
}

.service-content p {
  font-size: 1.6rem;
  margin: 1rem 2rem;
}

.service-container .service-content:hover {
  border-color: var(--main-color);
  /* scale: 1.02; */
}
.service-content img {
  width: 100%;
  height: 22rem;
  transition: .5 ease;
}

.service-content:hover img {
  transform: scale(1.1);
}

/*pets.css*/

.pets {
  background-color: var(--second-bg-color);
}

.pets h2 {
  margin: 0%;
  /* margin: 3%; */
  margin-bottom: 4%;
}

.pets-container {
  /* display: grid;
  grid-template-columns: repeat(3, 1fr); */
  width: 105%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 2.6rem;
}

.pets-container .pets-content {
  position: relative;
  border-radius: 2rem;
  box-shadow: 0 0 1rem var(--bg-color);
  overflow: hidden;
  display: flex;
  width: 30%;
}

.pets-content img {
  width: 100%;
  height: 22rem;
  transition: .5 ease;
}

.pets-content:hover img {
  transform: scale(1.1);
}

.pets-content .pets-layer {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: linear-gradient(rgba(0, 0, 0, .1), var(--main-color));
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transform: translateY(100%);
  transition: .5s ease;
}

.pets-content:hover .pets-layer {
  transform: translatey(0);
}

.pets-layer h4 {
  font-size: 3rem;
  margin-bottom: 0;
}

.pets-layer p {
  font-size: 1.6rem;
  text-align: center;
  padding: 2rem 2rem;
}

.pets-layer a {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
  height: 5rem;
  background: var(--text-color);
  border-radius: 50%;
  margin-bottom: 5rem;
}

.pets-layer a i {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: var(--second-bg-color);
  padding: 1rem;
}
.pets-layer h4,.pets-layer p,.pets-layer a{
  margin: 2%;
}

/* contact.css*/
.contact h2 {
  margin: 0%;
  margin-bottom: 3rem;
}

.contact form {
  max-width: 105%;
  /* margin: 1rem auto; */
  text-align: center;
  margin-bottom: 3rem;
  /* color: var(--text-color); */
}

.contact form .input-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.contact form .input-box input,
.contact form textarea {
  width: 100%;
  padding: 1.5rem;
  font-size: 1.6rem;
  color: var(--text-color);
  background: var(--second-bg-color);
  border-radius: .8rem;
  margin: .7rem 0;
}

.contact form .input-box input {
  width: 45%;
}

.contact form textarea {
  resize: none;
  max-width: 96%;
}

.contact form .btn {
  margin: 0%;
  margin-top: 4rem;
  cursor: pointer;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 1rem 9%;
  background: var(--second-bg-color);
}

.footer-text p {
  font-size: 1.6rem;
}

.footer-icon a {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: .8rem;
  background: var(--main-color);
  border-radius: 1rem;
  transition: .3 ease;
}

.footer-icon a:hover {
  box-shadow: 0 0 1rem var(--main-color);
}

.footer-icon a i {
  font-size: 2rem;
  color: var(--second-bg-color);
}
.toggle-btn{
  display: none;
}
